// @flow
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useCallback, useEffect } from 'react';
import moment from 'moment';
import Loading from 'commons/components/Loading';
import { Tabs, Tab } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
// import MainLayout from 'layout/MainLayout';
import TitleHeader from 'commons/components/TitleHeader';
import { listParkingLot } from 'mockData/listCompany';
import { getPosList, getCompanyList } from 'commons/redux';
import { getListInverter } from 'modules/accounts/redux';
import * as SignInAction from 'modules/accounts/redux';
import GroupSelectSidebar from 'commons/components/GroupSelectSidebar';
import {
  getStatisticsDevelopRaw,
  getStatisticDevelopChartData,
  getStatisticDevelopCard,
} from '../../redux';
import ItemContentTab from './ItemContentTab';

const OperationStatusPage = () => {
  const [menuTab, setMenuTab] = useState('');
  const { listStatusCompanySelect } = useSelector(
    (state) => state?.statusCompany
  );

  const { posList, isProcessing, comList } = useSelector(
    (state) => state?.commons
  );
  const { rawData, totalRawData, cardInfo, dataChart } = useSelector(
    (state) => state.statisticsDevelop
  );

  const defaultOption = {
    id: 1,
    value: 6,
    label: '6  ',
  };

  const defaultSearch = {
    posSelected: posList && posList[1] && posList[1].id,
    page: 1,
    classification: 'minute',
    startDate: new Date() || null,
    endDate: new Date() || null,
    vendorCompany: null,
    inverter: null,
    company: null,
    mockupType: null,
    parkingLot: null,
    insolation: true,
    performance: true,
    generation: true,
    pagination: defaultOption,
    inverter1: menuTab === '' ? null : [comList[1]],
  };

  const [paramsSearch, setParamsSearch] = useState(defaultSearch);

  const dataBoxContent = {
    day: (cardInfo?.prod_day && cardInfo?.prod_day.toLocaleString('en')) || '0',
    month:
      (cardInfo?.prod_month && cardInfo?.prod_month.toLocaleString('en')) ||
      '0',
    year:
      (cardInfo?.prod_sum && cardInfo?.prod_sum.toLocaleString('en')) || '0',
  };

  const dispatch = useDispatch();

  /**
   * get company list
   */
  const getListCompanyCallback = useCallback(() => {
    dispatch(getPosList());
  }, [dispatch]);

  useEffect(() => {
    getListCompanyCallback();
  }, [getListCompanyCallback]);

  useEffect(() => {
    setParamsSearch({
      ...paramsSearch,
      posSelected: posList && posList[1] && posList[1].id,
    });
  }, []);

  /**
   * get Event List data
   */
  const getListInverterCallback = useCallback(
    (params) => {
      dispatch(getListInverter(params));
    },
    [dispatch]
  );

  useEffect(() => {
    if (paramsSearch?.company) {
      getListInverterCallback({
        per_page: 9999,
        pos_id: paramsSearch?.posSelected,
      });
    }
  }, [getListInverterCallback, paramsSearch?.posSelected]);

  /**
   * get Device list
   */
  const getCompanyListCallback = useCallback(
    (params) => {
      dispatch(getCompanyList(params));
    },
    [dispatch]
  );

  useEffect(() => {
    if (paramsSearch?.posSelected) {
      getCompanyListCallback({ pos_id: paramsSearch?.posSelected });
    }
  }, [getCompanyListCallback, paramsSearch?.posSelected]);

  /**
   * get statistics generator data
   */
  const getStatisticsDevelopRawCallback = useCallback(
    (params) => {
      dispatch(getStatisticsDevelopRaw(params));
    },
    [dispatch]
  );

  useEffect(() => {
    if (paramsSearch?.posSelected) {
      getStatisticsDevelopRawCallback({
        com_id: menuTab,
        page: paramsSearch?.page,
        per_page: paramsSearch?.pagination?.value,
        pos_id: paramsSearch?.posSelected,
      });
    }
  }, [
    getStatisticsDevelopRawCallback,
    menuTab,
    paramsSearch?.page,
    paramsSearch?.pagination?.value,
    paramsSearch?.posSelected,
  ]);

  const handleChangeSearch = (item, name) => {
    switch (name) {
      case 'statusCompany':
        setParamsSearch({
          ...defaultSearch,
          company: item.id,
        });
        break;
      case 'mockupType':
        setParamsSearch({
          ...paramsSearch,
          mockupType: item.id,
        });
        break;
      case 'parkingLot':
        setParamsSearch({
          ...paramsSearch,
          parkingLot: item.id,
        });
        break;
      case 'generation':
        setParamsSearch({
          ...paramsSearch,
          generation: !item,
        });
        break;
      case 'performance':
        setParamsSearch({
          ...paramsSearch,
          performance: !item,
        });
        break;
      case 'insolation':
        setParamsSearch({
          ...paramsSearch,
          insolation: !item,
        });
        break;
      case 'classification':
        setParamsSearch({
          ...paramsSearch,
          classification: item,
        });
        break;
      case 'pagination':
        setParamsSearch({
          ...paramsSearch,
          pagination: item,
        });
        break;

      case 'inverter':
        setParamsSearch({
          ...paramsSearch,
          inverter: item,
        });
        break;
      case 'inverter1':
        setParamsSearch({
          ...paramsSearch,
          inverter1: item,
        });
        break;
      case 'vendorCompany':
        setParamsSearch({
          ...paramsSearch,
          vendorCompany: item,
          inverter: null,
        });
        dispatch(
          SignInAction.getListInverter({
            per_page: 0,
            com_id: item?.value,
          })
        );

        break;
      case 'page':
        setParamsSearch({
          ...paramsSearch,
          page: item,
        });
        break;
      case 'startDate':
        setParamsSearch({
          ...paramsSearch,
          startDate: item,
        });
        break;
      case 'endDate':
        setParamsSearch({
          ...paramsSearch,
          endDate: item,
        });
        break;
      case 'submitSearch':
        // call api update list table
        break;
      default:
        break;
    }
  };

  const onSelect = (eventKey) => {
    window.scrollTo(0, 0);
    setMenuTab(eventKey);
    const inverter1Selected = comList.find(
      (item) => item.id === parseInt(eventKey, 10)
    );

    if (eventKey === '') {
      setParamsSearch({
        ...defaultSearch,
        inverter1: null,
        posSelected: paramsSearch?.posSelected,
      });
    }
    setParamsSearch({
      ...defaultSearch,
      inverter1: inverter1Selected,
      posSelected: paramsSearch?.posSelected,
    });
  };

  let from;
  let to;
  if (paramsSearch?.startDate && paramsSearch?.endDate) {
    from = moment(paramsSearch?.startDate).format('YYYY-MM-DD');
    to = moment(paramsSearch?.endDate).format('YYYY-MM-DD');
  } else if (
    paramsSearch?.startDate &&
    !paramsSearch?.endDate &&
    (paramsSearch?.classification === 'minute' ||
      paramsSearch?.classification === 'hour')
  ) {
    from = moment(paramsSearch?.startDate).format('YYYY-MM-DD');
    to = moment(paramsSearch?.startDate).format('YYYY-MM-DD');
  } else if (!paramsSearch?.startDate && !paramsSearch?.endDate) {
    from = moment(new Date()).format('YYYY-MM-DD');
    to = moment(new Date()).format('YYYY-MM-DD');
  } else if (paramsSearch?.startDate && paramsSearch?.endDate) {
    from = moment(paramsSearch?.startDate).format('YYYY-MM-DD');
    to = moment(paramsSearch?.endDate).format('YYYY-MM-DD');
  } else if (
    paramsSearch?.startDate &&
    !paramsSearch?.endDate &&
    (paramsSearch?.classification === 'day' ||
      paramsSearch?.classification === 'month' ||
      paramsSearch?.classification === 'year')
  ) {
    from = moment(paramsSearch?.startDate).format('YYYY-MM-DD');
    to = moment(new Date()).format('YYYY-MM-DD');
  }

  const handleSubmitSearch = () => {
    dispatch(
      getStatisticDevelopChartData({
        pos_id: paramsSearch?.posSelected,
        ds_id: paramsSearch?.inverter?.id,
        com_id: paramsSearch?.inverter1?.id,
        from,
        to,
        type: paramsSearch?.classification,
        com_compare_id: paramsSearch?.inverter1?.id,
      })
    );
  };

  /**
   * get chart List data
   */
  const getStatisticDevelopChartDataCallback = useCallback(
    (params) => {
      dispatch(getStatisticDevelopChartData(params));
    },
    [dispatch]
  );

  useEffect(() => {
    if (paramsSearch?.posSelected) {
      getStatisticDevelopChartDataCallback({
        pos_id: paramsSearch?.posSelected,
        inverter_ids: menuTab,
      });
    }
  }, [
    getStatisticDevelopChartDataCallback,
    paramsSearch?.posSelected,
    menuTab,
  ]);

  /**
   * get chart List data
   */
  const getStatisticDevelopCardCallback = useCallback(
    (params) => {
      dispatch(getStatisticDevelopCard(params));
    },
    [dispatch]
  );

  useEffect(() => {
    if (paramsSearch?.posSelected) {
      getStatisticDevelopCardCallback({
        pos_id: paramsSearch?.posSelected,
        inverter_ids: menuTab,
      });
    }
  }, [getStatisticDevelopCardCallback, paramsSearch?.posSelected, menuTab]);

  return (
    <>
      {isProcessing && <Loading />}
      <div className="content-wrap">
        <TitleHeader title="  " />
        <div className="content-body page-company">
          <GroupSelectSidebar
            handleChangeSearch={handleChangeSearch}
            listParkingLot={listParkingLot}
            paramsSearch={paramsSearch}
            listStatusCompanySelect={posList.slice(1)}
          />
          <div className="content-body-left w-100">
            <div className="h-100">
              <Tabs
                defaultActiveKey=""
                className="list-order tab-list"
                onSelect={(eventKey) => onSelect(eventKey)}
              >
                {comList &&
                  comList.map((com) => (
                    <Tab
                      eventKey={com.id}
                      title={
                        <div className="tab-name">
                          {com?.label}
                          {com?.label !== '' && <span>{com?.id}</span>}
                        </div>
                      }
                    >
                      <ItemContentTab
                        dataBoxContent={dataBoxContent}
                        rawData={
                          rawData &&
                          rawData.map((raw, index) => ({
                            rowId:
                              `${
                                totalRawData -
                                (paramsSearch?.page - 1) *
                                  paramsSearch?.pagination?.value -
                                index
                              }` || '',
                            dateTime: moment(raw.dm_datetime).format(
                              'YYYY-MM-DD hh:mm:ss'
                            ),
                            companyName: raw?.com_name || '',
                            inverterID: raw?.ds_id || '',
                            inverterName: raw?.ds_name || '',
                            installationLocation: raw?.pos_name || '',
                            dmProddDay:
                              raw?.dm_prod_day &&
                              `${raw?.dm_prod_day.toLocaleString('en')}KWH`,
                            dmProddMonth:
                              raw?.dm_prod_month &&
                              `${raw?.dm_prod_month.toLocaleString('en')}KWH`,
                            dmProdSum:
                              raw?.dm_prod_sum &&
                              `${raw?.dm_prod_sum.toLocaleString('en')}MW`,
                            dmPerformanceRatio: `${raw?.dm_performance_ratio}%`,
                          }))
                        }
                        dataContent={{}}
                        totalPage={totalRawData}
                        perPage={paramsSearch?.pagination?.value}
                        listInverter={comList.slice(1)}
                        listStatusCompanySelect={listStatusCompanySelect}
                        paramsSearch={paramsSearch}
                        handleChangeSearch={handleChangeSearch}
                        activeTab={menuTab}
                        handleSubmitSearch={handleSubmitSearch}
                        dateTime={{
                          from,
                          to,
                        }}
                        chartData={dataChart}
                        id={com.id}
                      />
                    </Tab>
                  ))}
              </Tabs>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default OperationStatusPage;
